 * @LastEditors: yd
 * @LastEditTime: 2020-09-05 18:32:26
--> 
<template>
  <div>
    <mrc_ess_iconGroup :is_disabled='disabled'
                       :showReturn='false'
                       :show_del='false'
                       @handleEdit='handleEdit'></mrc_ess_iconGroup>
    <v-card class="pt-10 pl-8 pr-8">
      <v-row>
        <v-col cols="2"
               class="f1">个人资料</v-col>
        <!-- <button @click="cs">sasa</button> -->
        <!-- <mrc_ess_advanceFilterAndSearchInput></mrc_ess_advanceFilterAndSearchInput> -->
      </v-row>
      <!-- <v-col cols="12">
        <mrc_ess_autoComplete v-model="staffUuid"
                              :dropDownList=dropDownList></mrc_ess_autoComplete>
      </v-col> -->
      <v-row>
        <v-col cols="2">
          <span class="d-flex flex-column pl-4 pt-4 ">
            <v-img src="https://picsum.photos/510/300?random"
                   class="mb-4"
                   aspect-ratio="2"></v-img>
            <span style="color:#90A4AE;margin:0 auto;">此照片不公开</span>
          </span>
        </v-col>
        <v-col cols="10">
          <v-row>
            <v-col cols="4">
              <v-text-field label="名字*"
                            :readonly="disabled"
                            :error-messages="$errors('stafflist','preferred_name',requiredRule.concat(nocharacter))"
                            v-model="stafflist.preferred_name"
                            append-icon="mdi-longitude">
              </v-text-field>
            </v-col>
            <v-col cols="4">
              <v-text-field label="姓氏*"
                            :readonly="disabled"
                            :error-messages="$errors('stafflist','family_name',requiredRule.concat(nocharacter))"
                            v-model="stafflist.family_name"
                            append-icon="mdi-longitude">
              </v-text-field>
            </v-col>
            <v-col cols="4">
              <v-text-field label="小名"
                            :readonly="disabled"
                            :error-messages="$errors('stafflist','family_name',nocharacter)"
                            v-model="stafflist.child_name"
                            append-icon="mdi-longitude">
              </v-text-field>
            </v-col>
            <v-col cols="8">
              <v-text-field label="显示名字*"
                            :error-messages="$errors('stafflist','show_name',requiredRule.concat(nocharacter))"
                            :readonly="disabled"
                            v-model="stafflist.show_name">
              </v-text-field>
            </v-col>
            <v-col cols="4">
              <v-text-field label="员工ID*"
                            :error-messages="$errors('stafflist','tricode',requiredRule.concat(nocharacter))"
                            :readonly="disabled"
                            v-model="stafflist.tricode">
              </v-text-field>
            </v-col>
            <v-col cols="6">
              <mrc_ess_datePicker @getTime='paramsChange'
                                  :disabled="disabled"
                                  label='出生日期'
                                  v-model="stafflist.date_of_brith"></mrc_ess_datePicker>
            </v-col>
            <v-col cols="6">
              <v-text-field label="性别"
                            hide-details="auto"
                            :disabled='disabled'
                            v-model="sex"></v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field label="婚姻状况"
                            hide-details="auto"
                            v-model="stafflist.maritual_status"
                            :readonly='disabled'></v-text-field>
            </v-col>
            <v-col cols="6">
            </v-col>
          </v-row>

          <v-row v-if="!disabled">
            <v-col cols='2'>
              <mrc_ess_prompt :label="'区号'"
                              :disabled='disabled'
                              :rules='false'
                              :chips="false"
                              v-model="stafflist.mobile_number_area_code"
                              obj="country_mobile_prefix">
              </mrc_ess_prompt>
            </v-col>
            <v-col cols='4'>
              <!-- <v-text-field label="手机电话号码"
                            v-model="stafflist.mobile_number"
                            :error-messages="$errors('stafflist','mobile_number',[{rule:'areaRequired',msg:'区号没填'},{rule:'phoneVerify',msg:'电话号码格式不对,请输入正确的号码格式'}])"
                            hide-details="auto"
                            :readonly='disabled'></v-text-field> -->
              <v-text-field label="手机电话号码"
                            v-model="stafflist.mobile_number"
                            hide-details="auto"
                            :readonly='disabled'></v-text-field>
            </v-col>
            <v-col cols='2'>
              <mrc_ess_prompt :label="'区号'"
                              :disabled='disabled'
                              :rules='false'
                              :chips="false"
                              v-model="stafflist.home_number_area_code"
                              obj="country_mobile_prefix">
              </mrc_ess_prompt>
            </v-col>
            <v-col cols='4'>
              <!-- <v-text-field label="住宅电话号码"
                            v-model.number="stafflist.home_number"
                            :error-messages="$errors('stafflist','home_number',[{rule:'areaRequired',msg:'区号没填'},{rule:'phoneVerify',msg:'电话号码格式不对,请输入正确的号码格式'}])"
                            hide-details="auto"
                            :readonly='disabled'></v-text-field> -->
              <v-text-field label="住宅电话号码"
                            v-model.number="stafflist.home_number"
                            hide-details="auto"
                            :readonly='disabled'></v-text-field>
            </v-col>
          </v-row>
          <v-row v-else>
            <v-col cols='6'>
              <v-text-field label="手机电话号码"
                            v-model.number="mobile_number"
                            hide-details="auto"
                            :readonly='disabled'></v-text-field>
            </v-col>
            <v-col cols='6'>
              <v-text-field label="住宅电话号码"
                            v-model.number="home_number"
                            hide-details="auto"
                            :readonly='disabled'></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="6">
              <v-text-field label="个人邮箱*"
                            :error-messages="$errors('stafflist','persona_email_address',emailRule)"
                            :readonly="disabled"
                            v-model="stafflist.persona_email_address">
              </v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field label="传真号码*"
                            :error-messages="$errors('stafflist','fax',numericRule)"
                            :readonly="disabled"
                            v-model.number="stafflist.fax">
              </v-text-field>
            </v-col>
          </v-row>
        </v-col>
      </v-row>

      <span class="d-flex justify-space-between mb-5">
        <span class="f1">地址</span>
        <v-btn rounded
               color="#e3bec8"
               dark
               @click="editItem"
               v-if="!disabled">+新住址</v-btn>
      </span>
      <v-card v-for="(item,index) in mrbase_sys_staff_address"
              :key="index"
              class="bigcard">
        <v-card-title class="addresscard">
          <div>
            <v-btn rounded
                   color="rgb(19, 166, 113)"
                   dark
                   x-small
                   v-if="item.status==1">有效</v-btn>
            <v-btn rounded
                   color="#cccccc"
                   dark
                   x-small
                   v-else>失效</v-btn>
            <span class="sps2">住址{{index+1}}</span>
          </div>
          <div v-if="!disabled">
            <v-icon class="mr-2"
                    @click="editItem(item)">
              mdi-pencil
            </v-icon>
            <v-icon @click="deleteItem(item)">
              mdi-delete
            </v-icon>
          </div>
        </v-card-title>
        <v-card-text>
          <v-text-field label="地址*"
                        v-model="item.address_line_1"
                        :readonly="disabled">
          </v-text-field>
          <v-text-field :readonly="disabled"
                        v-model="item.address_line_2">
          </v-text-field>
          <v-text-field :readonly="disabled"
                        v-model="item.address_line_3">
          </v-text-field>
        </v-card-text>
      </v-card>
      <!------------------- 分割线---------------- -->
      <span class="d-flex justify-space-between mb-5">
        <span class="f1">紧急联络人</span>
        <v-btn rounded
               color="#ffffff"
               dark
               class="bt1"
               @click="editItemtel"
               v-if='!disabled'>
          <span class="sp1">+</span>
          新紧急联系人
        </v-btn>
      </span>
      <v-card v-for="(item,index) in tellist"
              :key="index"
              class="m2">

        <v-card-title class="telheader">
          <span class="c1">紧急联系人{{index+1}}</span>
          <div v-if="!iconstatus">
            <v-icon class="mr-2"
                    @click="editItemtel(item)">
              mdi-pencil
            </v-icon>
            <v-icon @click="deleteItemtel(item)">
              mdi-delete
            </v-icon>
          </div>
        </v-card-title>
        <v-card-text>
          <v-row>
            <v-col cols="6">
              <v-text-field label="关系"
                            :readonly="iconstatus"
                            append-icon="mdi-longitude"
                            v-model="item.emergency_contact_relationship">
              </v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field label="全名字"
                            :readonly="iconstatus"
                            append-icon="mdi-longitude"
                            v-model="item.emergency_contact_given_name">
              </v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field label="联系电话"
                            :readonly="iconstatus"
                            v-model="item.phone_number">
              </v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field label="邮箱"
                            :readonly="iconstatus"
                            v-model="item.emergency_contact_email">
              </v-text-field>
            </v-col>
            <v-col cols="12">
              <v-text-field class="m3"
                            label="通讯地址"
                            :readonly="iconstatus"
                            height="25"
                            append-icon="mdi-longitude"
                            v-model="item.emergency_contact_address_line_1">
              </v-text-field>
            </v-col>
            <v-col cols="12">
              <v-text-field class="m3"
                            :readonly="iconstatus"
                            height="25"
                            append-icon="mdi-longitude"
                            v-model="item.emergency_contact_address_line_2">
              </v-text-field>
            </v-col>
            <v-col cols="12">
              <v-text-field class="m3"
                            :readonly="iconstatus"
                            height="25"
                            append-icon="mdi-longitude"
                            v-model="item.emergency_contact_address_line_3">
              </v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>

      <mrc_ess_submitBtn @success='success'
                         @cancel='cancel'
                         :loading='btnIsLoading'
                         v-if='!disabled'></mrc_ess_submitBtn>
      <!-- 地址弹出框 -->
      <mrc_ess_myDialog v-model="dialog"
                        title="新层级"
                        @success='save'
                        @cancel='close'>
        <v-card-text>
          <v-text-field label="地址*"
                        :error-messages="$errors('editedItem','address_line_1',requiredRule)"
                        v-model="editedItem.address_line_1">
          </v-text-field>
          <v-text-field :error-messages="$errors('editedItem','address_line_2',requiredRule)"
                        v-model="editedItem.address_line_2">
          </v-text-field>
          <v-text-field :error-messages="$errors('editedItem','address_line_3',requiredRule)"
                        v-model="editedItem.address_line_3">
          </v-text-field>
          <mrc_ess_datePicker @getTime='paramsChange2'
                              label='有效日期*'
                              :disabled='false'
                              :errMsg="$errors('editedItem','effective_start_date',requiredRule)"
                              v-model="editedItem.effective_start_date"></mrc_ess_datePicker>
        </v-card-text>
      </mrc_ess_myDialog>
      <!-- 紧急联系人弹出框 -->
      <mrc_ess_myDialog v-model="dialog2"
                        title="新紧急联系人"
                        :width='800'
                        @success='telsave'
                        @cancel='telclose'>
        <v-card-text>
          <v-row>
            <v-col cols="12">
              <v-text-field label="关系*"
                            :readonly="iconstatus"
                            :error-messages="$errors('editedtelItem','emergency_contact_relationship',requiredRule.concat(nocharacter))"
                            append-icon="mdi-longitude"
                            v-model="editedtelItem.emergency_contact_relationship">
              </v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field label="名字*"
                            :readonly="iconstatus"
                            :error-messages="$errors('editedtelItem','emergency_contact_given_name',requiredRule.concat(nocharacter))"
                            append-icon="mdi-longitude"
                            v-model="editedtelItem.emergency_contact_given_name">
              </v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field label="姓氏*"
                            :readonly="iconstatus"
                            append-icon="mdi-longitude"
                            :error-messages="$errors('editedtelItem','emergency_contact_family_name',requiredRule.concat(nocharacter))"
                            v-model="editedtelItem.emergency_contact_family_name">
              </v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field label="其他名*"
                            :readonly="iconstatus"
                            :error-messages="$errors('editedtelItem','emergency_contact_christian_name',requiredRule.concat(nocharacter))"
                            append-icon="mdi-longitude"
                            v-model="editedtelItem.emergency_contact_christian_name">
              </v-text-field>
            </v-col>
            <v-col cols="6"></v-col>
            <v-col cols="2">
              <mrc_ess_prompt :label="'区号*'"
                              :disabled='iconstatus'
                              :rules='false'
                              :chips="false"
                              :errMsg="$errors('editedtelItem','emergency_contact_area_code',requiredRule)"
                              v-model="editedtelItem.emergency_contact_area_code"
                              obj="country_mobile_prefix">
              </mrc_ess_prompt>
            </v-col>

            <v-col cols="4">
              <!-- <v-text-field label="联系电话*"
                            :readonly="iconstatus"
                            :error-messages="$errors('editedtelItem','emergency_contact_phone_number',[{rule:'areaRequired',msg:'区号没填'},{rule:'phoneVerify',msg:'电话号码格式不对,请输入正确的号码格式'}].concat(requiredRule))"
                            v-model="editedtelItem.emergency_contact_phone_number">
              </v-text-field> -->
              <v-text-field label="联系电话*"
                            :readonly="iconstatus"
                            v-model="editedtelItem.emergency_contact_phone_number">
              </v-text-field>
            </v-col>
            <v-col cols="6">
              <v-text-field label="邮箱"
                            :error-messages="$errors('editedtelItem','emergency_contact_email',emailRule)"
                            :readonly="iconstatus"
                            v-model="editedtelItem.emergency_contact_email">
              </v-text-field>
            </v-col>
            <v-col cols="12">
              <v-text-field class="m3"
                            label="通讯地址"
                            :readonly="iconstatus"
                            height="25"
                            append-icon="mdi-longitude"
                            v-model="editedtelItem.emergency_contact_address_line_1">
              </v-text-field>
            </v-col>
            <v-col cols="12">
              <v-text-field class="m3"
                            :readonly="iconstatus"
                            height="25"
                            append-icon="mdi-longitude"
                            v-model="editedtelItem.emergency_contact_address_line_2">
              </v-text-field>
            </v-col>
            <v-col cols="12">
              <v-text-field class="m3"
                            :readonly="iconstatus"
                            height="25"
                            append-icon="mdi-longitude"
                            v-model="editedtelItem.emergency_contact_address_line_3">
              </v-text-field>
            </v-col>
          </v-row>
        </v-card-text>
      </mrc_ess_myDialog>
    </v-card>
  </div>
</template>

<script>
import { required, maxLength, between, email, url, numeric } from 'vuelidate/lib/validators'
import { getStaffPersonal, getStaffPersonalUpdate } from '@/service/api/staffmsg.js'
import { addFile } from '@/service/api/fileManagements.js'
import moment from 'moment'
import index4 from './index4'
export default {
  components: { index4 },
  props: {
    iconstatus: {
      type: Boolean
    }
  },
  data () {
    return {
      dropDownList: [
        { show_name: 'xx' },
        { show_name: '12' },
        { show_name: 'qw' }
      ],
      fn: getStaffPersonal,
      errorMessages: [],
      staffUuid: '',
      nums: 1,
      btnIsLoading: false,
      dialog: false,
      dialog2: false,
      disabled: true,// 默认只读 
      uuid: '',//唯一标识
      staff_uuid: '',  //关联staff表uuid
      stafflist: {},
      mrbase_sys_staff_address: [    //地址信息
      ],
      editedIndex: -1,
      editedItem: {
        address_line_1: "",    //地址1
        address_line_2: "",    //地址2
        address_line_3: "",    //地址3
        effective_start_date: '',    //地址有效日期
      },
      defaultItem: {
        address_line_1: "",    //地址1
        address_line_2: "",    //地址2
        address_line_3: "",    //地址3
        effective_start_date: '',    //地址有效日期
      },
      deleteAddresss: [], //删除的地址 
      // -----------------------分割线-------------
      tellist: [],//紧急联系人列表
      deletetellist: [], //删除紧急联系人列表
      editedtelIndex: -1,
      editedtelItem: {
        emergency_contact_relationship: '',
        emergency_contact_given_name: '',
        emergency_contact_family_name: '',
        emergency_contact_christian_name: '',
        emergency_contact_area_code: '',
        emergency_contact_phone_number: '',
        emergency_contact_email: '',
        emergency_contact_address_line_1: '',
        emergency_contact_address_line_2: '',
        emergency_contact_address_line_3: '',
      },
      defaulteltItem: {
        emergency_contact_relationship: '',
        emergency_contact_given_name: '',
        emergency_contact_family_name: '',
        emergency_contact_christian_name: '',
        emergency_contact_area_code: '',
        emergency_contact_phone_number: '',
        emergency_contact_email: '',
        emergency_contact_address_line_1: '',
        emergency_contact_address_line_2: '',
        emergency_contact_address_line_3: '',
      },
      // 上传文件的文件名
      uploadFileName: '',
      // 上传文件的错误信息提示
      uploadFileErrMsg: '',
      // 文件的最大尺寸
      fileMaxSize: 2 * 1024 * 1024,
      // 文件接受的格式
      fileAcceptType: 'application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
    };
  },
  created () { },
  async mounted () {
    console.log(typeof (this.fn));
    await this.getlist()
  },
  beforeDestroy () {
    this.$store.commit('set_iconstatus', '')
  },
  methods: {
    cs () {
      console.log(this.staffUuid)
    },
    handleEdit () {
      this.disabled = !this.disabled
      this.$store.commit('set_iconstatus', this.readonly)
    },
    success () { //提交  moment(time).format('YYYY-MM-DD') 
      this.$v.stafflist.$touch()
      if (this.$v.stafflist.$error) return
      this.btnIsLoading = true
      const user_id = this.$store.state.detailuuid
      const uuid = this.$store.state.uuid
      this.stafflist.gender = Number(this.stafflist.gender)
      // console.log(this.stafflist.gender);
      if (this.stafflist.date_of_brith) this.stafflist.date_of_brith = moment(this.stafflist.date_of_brith).format('YYYY-MM-DD')
      this.mrbase_sys_staff_address.map(item => {
        if (item.effective_start_date) item.effective_start_date = moment(item.effective_start_date).format('YYYY-MM-DD')
      })
      getStaffPersonalUpdate({
        uuid: uuid,
        user_id: user_id,
        mrbase_sys_staff_personal_info: [this.stafflist],
        mrbase_sys_staff_address: [...this.mrbase_sys_staff_address, ...this.deleteAddresss]
      }).then(async res => {
        if (res.code === '200') {
          this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
          await this.getlist()
        }
        this.btnIsLoading = false
      })
    },
    cancel () {
      this.$myConfirm({
        content: '确认取消编辑'
      }).then(res => {
        this.$router.push({
          name: 'staffmanagement'
        })
      }).catch(err => { })
    },
    paramsChange () {  //时间改变获取时间
    },
    paramsChange2 () {

    },
    editItem (item) {
      if (item.uuid || item.staff_uuid) {  //传了item就是修改  没传就是添加
        this.editedIndex = this.mrbase_sys_staff_address.indexOf(item)
        this.editedItem = Object.assign({}, item)
      }
      this.dialog = true
    },
    deleteItem (item) {
      const index = this.mrbase_sys_staff_address.indexOf(item)
      this.$myConfirm({
        content: '确认要删除该地址吗'
      }).then(() => {
        const dd = this.mrbase_sys_staff_address.splice(index, 1)
        if (dd[0].uuid || dd[0].staff_uuid) this.deleteAddresss.push({ ...dd[0], "~type~": 'del' })
      })
    },
    async getlist () {
      const uuid = this.$store.state.uuid
      const res = await getStaffPersonal({ staff_uuid: uuid })
      if (Object.keys(res.data).length > 0) {
        this.uuid = res.data.uuid
        this.staff_uuid = res.data.staff_uuid   //后端未返回字段
        this.stafflist = this.$deletefield(null, res.data)
        this.mrbase_sys_staff_address = res.mrbase_sys_staff_address
      } else {

      }
    },
    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },
    save () {
      this.$v.editedItem.$touch()
      if (this.$v.editedItem.$error) return
      if (this.editedIndex > -1) {
        Object.assign(this.mrbase_sys_staff_address[this.editedIndex], this.editedItem)
      } else {
        this.mrbase_sys_staff_address.push(this.editedItem)
      }
      this.close()
    },
    editItemtel (item) {  //紧急联系人
      this.$v.editedtelItem.$reset()
      if (item.uuid || item.staff_uuid || item.emergency_contact_given_name) {
        this.editedtelIndex = this.tellist.indexOf(item)
        this.editedtelItem = Object.assign({}, item)
      }
      this.dialog2 = true
    },
    deleteItemtel (item) {
      const index = this.tellist.indexOf(item)
      this.$myConfirm({
        content: '确认要删除吗'
      }).then(() => {
        const dd = this.tellist.splice(index, 1)
        if (dd[0].uuid || dd[0].staff_uuid) this.deletetellist.push({ uuid: dd[0].uuid, "~type~": 'del' })
        // console.log(this.deletetellist);
      })
    },
    telsave () {  //紧急联系人 电话弹出框
      this.$v.editedtelItem.$touch()
      if (this.$v.editedtelItem.$error) return
      if (this.editedtelIndex > -1) {
        Object.assign(this.tellist[this.editedtelIndex], this.editedtelItem)
      } else {
        // console.log(this.editedtelItem);
        this.tellist.push(this.editedtelItem)
      }
      this.telclose()
    },
    telclose () {
      this.dialog2 = false
      this.$nextTick(() => {
        this.editedtelItem = Object.assign({}, this.defaulteltItem)
        this.editedtelIndex = -1
      })
    },
  },
  computed: {
    mobile_number () {
      if (this.stafflist.mobile_number_area_code && this.stafflist.mobile_number) {
